* {
  margin: 0px;
  padding: 0px;
  letter-spacing: 3px;
}
* {
  font-family: "Poppins", sans-serif;
}

button {
  background-color: #00b2a9;
  border: none;
  border-radius: 10px;
  color: #eeeeee;
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  text-decoration: none;
  letter-spacing: 2px;
}
a {
  font-family: "Poppins", sans-serif;
  cursor: pointer;
  text-decoration: none;
  letter-spacing: 4px;
  color: #eeeeee;
}
a:hover {
  color: #fbee23;
}
button:hover {
  background-color: #ffc659;
  color: white;
  outline: none;
  border: 0;
}
