.books {
  padding-top: 11vh;
  margin-bottom: 7vh;
  text-align: center;
}
.books h1 {
  padding-top: 3vh;
  letter-spacing: 5px;
  color: #777;
  font-size: 5vh;
}

.books p,
h6 {
  color: #999;
  padding-top: 4px;
}
.books button {
  margin-top: 2vh;
  width: 18vh;
  height: 4vh;
  background-color: #999;
  border-radius: 7px;
  outline-style: dotted 1px;
  outline-color: #eeeeee;
  border: none;
}
.books button:hover {
  background-color: #ffc659;
  color: white;
  outline: none;
  border: 0;
}
@media screen and (max-width: 786px) {
  .books h3 {
    font-size: medium;
  }
  .books h1 {
    font-size: large;
    font-style: normal;
  }
  .books button {
    margin-top: 2vh;
    width: 12vh;
    height: 4vh;
    background-color: #999;
    border-radius: 7px;
    outline-style: dotted 1px;
    outline-color: #eeeeee;
    border: none;
  }
}
