.home {
  background-image: url(../../images/bk.jpg);
  background-size: cover;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #eeeeee;
  size: 100%;
}
.home_btn {
  padding-top: 25%;
  padding-bottom: 13%;
}
.home button {
  margin-top: 2vh;
  width: 20%;
  height: 8vh;
  margin-bottom: 2vh;
}

@media screen and (max-width: 786px) {
  .home {
    background-image: url(../../images/bk.jpg);
    background-size: cover;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #eeeeee;
    size: 100%;
  }
  .home_btn {
    padding-top: 25%;
    padding-bottom: 13%;
    padding-right: 5%;
  }
  .home h1 {
    font-size: 2vh;
    align-items: center;
    font-style: normal;
    margin: 0 auto;
  }
  .home h2 {
    font-size: 1.5vh;
    align-items: center;
  }
  .home button {
    width: 40%;
    height: 4vh;
  }
  .home button strong {
    font-size: 10px;
  }
}
