.form_main {
  padding-top: 11vh;
}
form {
  background-size: cover;
  width: 30%;
  margin: 3% 35% 5% 35%;
  border-radius: 7px;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 2.5px 2.5px -3px rgba(0, 0, 0, 0.043),
    0 5.9px 5.8px -3px rgba(0, 0, 0, 0.056),
    0 10.5px 10.4px -3px rgba(0, 0, 0, 0.066),
    0 17.5px 17.2px -3px rgba(0, 0, 0, 0.079),
    0 28.8px 28.3px -3px rgba(0, 0, 0, 0.099),
    0 50.4px 49.5px -3px rgba(0, 0, 0, 0.131),
    0 109px 107px -3px rgba(0, 0, 0, 0.18);
  background-color: #eef2f7;
  /* background: #7777; */
  /* background-color: #eeeeee; */
}
form label {
  font-size: small;
}

form input {
  width: 90%;
  height: 5vh;
  justify-content: center;
  margin-top: 1vh;
  font-family: "Poppins", sans-serif;
  outline-color: slategrey;
  border-radius: 5px;
  border: none;
  text-indent: 1.5vh;
  letter-spacing: 1px;
}
.formHead {
  padding-bottom: 1rem;
  font-size: 4vh;
}
form p {
  font-size: small;
}
form button {
  margin: 2vh;
  width: 18vh;
  height: 6vh;
  background-color: #00b2a9;
  border-radius: 7px;
  outline-style: dotted 1px;
  outline-color: #eeeeee;
  border: none;
}
form section {
  margin-top: 1vh;
}
.cancelButton {
  background-color: red;
}

@media screen and (max-width: 786px) {
  form {
    background-size: cover;
    width: 70%;
    margin: 3% 15% 5% 15%;
    border-radius: 7px;
    justify-content: center;
    align-items: center;
    text-align: center;
    box-shadow: 0 2.5px 2.5px -3px rgba(0, 0, 0, 0.043),
      0 5.9px 5.8px -3px rgba(0, 0, 0, 0.056),
      0 10.5px 10.4px -3px rgba(0, 0, 0, 0.066),
      0 17.5px 17.2px -3px rgba(0, 0, 0, 0.079),
      0 28.8px 28.3px -3px rgba(0, 0, 0, 0.099),
      0 50.4px 49.5px -3px rgba(0, 0, 0, 0.131),
      0 109px 107px -3px rgba(0, 0, 0, 0.18);
    background-color: #eef2f7;
    /* background: #7777; */
    /* background-color: #eeeeee; */
  }
  form input {
    height: 3vh;
    justify-content: center;
    margin-top: 1vh;
    font-family: "Poppins", sans-serif;
    outline-color: slategrey;
    border-radius: 5px;
    border: none;
    text-indent: 1.5vh;
    letter-spacing: 1px;
  }
  form button {
    margin: 1vh;
    width: 18vh;
    height: 4vh;
    background-color: #00b2a9;
    border-radius: 7px;
    outline-style: dotted 1px;
    outline-color: #eeeeee;
    border: none;
  }
  form label {
    font-size: x-small;
  }
  form strong {
    font-size: x-large;
  }
}
