.authors {
  padding-top: 11vh;
  margin-bottom: 2vh;
  text-align: center;
}
.authors h1 {
  padding-top: 3vh;
  letter-spacing: 5px;
  color: #777;
  font-size: 6vh;
}
.authors button {
  margin: 5vh;
  width: 18vh;
  height: 4vh;
  background-color: #999;
  border-radius: 7px;
  outline-style: dotted 1px;
  outline-color: #eeeeee;
  border: none;
}
.authors button:hover {
  background-color: #ffc659;
  color: white;
  outline: none;
  border: 0;
}
@media screen and (max-width: 786px) {
  .authors h1 {
    font-size: large;
    font-style: normal;
  }
  .authors button {
    margin: 0.7vh;
    width: 18vh;
    height: 4vh;
    background-color: #777;
    border-radius: 7px;
    outline-style: dotted 1px;
    outline-color: #eeeeee;
    border: none;
  }
}
