.bookDetails button {
  margin-top: 1vh;
  width: 18vh;
  height: 4vh;
  background-color: red;
  border-radius: 7px;
  outline-style: dotted 1px;
  outline-color: #eeeeee;
  border: none;
}
@media screen and (max-width: 786px) {
  .bookDetails button {
    margin-top: 1vh;
    width: 12vh;
    height: 3vh;
  }
}
