.author {
  padding: 2vh;
  background-color: white;
  border-radius: 10px;
  box-shadow: 6px 6px 10px -1px rgba(0, 0, 0, 0.15),
    -6px -6px 10px -1px rgba(255, 255, 255, 0.7);
  border: 1px solid rgba(0, 0, 0, 0.05);
  text-align: center;
  margin-bottom: 1vh;
  margin-top: 1vh;
  margin-left: 15%;
  margin-right: 15%;
}
.author h2 {
  letter-spacing: 2px;
}
.author hr {
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.authorDetails {
  padding-top: 1vh;
}
.authorDetails button {
  margin: 0.7vh;
  width: 18vh;
  height: 4vh;
  background-color: #00b2a9;
  border-radius: 7px;
  outline-style: dotted 1px;
  outline-color: #eeeeee;
  border: none;
}
.authorDetails button:hover {
  background-color: #ffc659;
  color: white;
  outline: none;
  border: 0;
}
.authorDetails strong {
  font-size: 10px;
  color: #777;
  padding-right: 2vh;
  padding-left: 2vh;
}
.authorDetails small {
  padding-right: 2vh;
  padding-left: 2vh;
}
@media screen and (max-width: 786px) {
  .author {
    padding: 1vh;
    background-color: white;
    border-radius: 10px;
    box-shadow: 6px 6px 10px -1px rgba(0, 0, 0, 0.15),
      -6px -6px 10px -1px rgba(255, 255, 255, 0.7);
    border: 1px solid rgba(0, 0, 0, 0.05);
    text-align: center;
    margin-bottom: 1vh;
    margin-top: 1vh;
    margin-left: 10%;
    margin-right: 10%;
  }
  .author h2 {
    font-size: large;
  }
  .author p {
    font-size: xx-small;
    letter-spacing: 2px;
  }
  .authorDetails button {
    margin: 0.7vh;
    width: 12vh;
    height: 3vh;
    background-color: #00b2a9;
    border-radius: 7px;
    outline-style: dotted 1px;
    outline-color: #eeeeee;
    border: none;
  }
}
