header {
  display: flex;
  width: 100%;
  margin: auto;
  height: 11vh;
  background: #00b2a9;
  text-align: center;
  position: fixed;
  color: #eeeeee;
  justify-content: space-around;
  align-items: center;
}
.title {
  flex: 3;
}
.header_links {
  flex: 7;
}
.header_links a {
  margin-left: 7vh;
}
.add_btn {
  flex: 2;
}
.add_btn button {
  border-radius: 5px;
  height: 6vh;
  width: 60%;
  background-color: #eeeeee;
  color: #00b2a9;
}
.add_btn button:hover {
  border-radius: 5px;
  height: 6vh;
  width: 60%;
  background-color: #ffc659;
  color: #00b2a9;
}

@media screen and (max-width: 786px) {
  .title {
    flex: 1;
    padding-right: 2px;
  }
  .header_links {
    flex: 2;
  }
  .header_links a {
    margin-left: 1vh;
  }
  .add_btn {
    flex: 1;
  }
  .title h3 {
    font-size: small;
  }
  .header_links {
    font-size: x-small;
  }
  .add_btn button {
    width: 70%;
  }
}
